import { ButtonWebElementBase } from './ButtonWebElementBase';
import { By } from 'selenium-webdriver';

/**
 * The component WebElement for [oj-c-button](../../../oj-c/docs/oj.Button.html).
 * Do not instantiate this class directly, instead, use
 * [findButton](../functions/findButton.html).
 */
export class ButtonWebElement extends ButtonWebElementBase {
  // Put overrides here

  /**
   * Perform a click on the button
   */
  doAction() {
    return this.click();
  }

  /**
   * Perform a click on the button
   */
  async click() {
    // Find the <button> element to click so that it can receive focus
    const button = await this.findElement(By.css('button'));
    return button.click();
  }
}
